import { PlusOutlined } from '@ant-design/icons';
import { Button, Col, DatePicker, Form, Input, InputNumber, message, Row, Select, Space, Spin, Tabs, Upload } from 'antd';
import moment from 'moment';
import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import ReactQuill from 'react-quill';
import './index.scss';
import { toast } from 'react-toastify';
import UserApis from '../../../../../apis/User';
import baseURL from '../../../../../utils/url';
import SpecialtyApis from '../../../../../apis/Specialty';
import DoctorApis from '../../../../../apis/Doctor';
import BackIcon from '../../../../../components/Icon/Common/BackIcon';
import { listGender } from '../../../../../common/constants/gender';
import { listPayment, listPositon, listRole } from '../../../../../common/constants/doctor';

const { TextArea } = Input;
const { Option } = Select;

const modulesQill = {
  toolbar: [
    [{ 'header': '1' }, { 'header': '2' }, { 'font': [] }],
    [{ size: [] }],
    ['bold', 'italic', 'underline', 'strike', 'blockquote'],
    [{ 'list': 'ordered' }, { 'list': 'bullet' },
    { 'indent': '-1' }, { 'indent': '+1' }],
    ['link', 'image', 'video'],
    ['clean']
  ],
  clipboard: {
    matchVisual: false,
  },
  history: {
    delay: 1000,
    maxStack: 50,
    userOnly: false
  },
  // imageResize: {
  // displayStyles: {
  //   backgroundColor: 'black',
  //   border: 'none',
  //   color: 'white'
  // },
  // modules: ['Resize', 'DisplaySize', 'Toolbar']
  // },
}
const formats = [
  'header', 'font', 'size',
  'bold', 'italic', 'underline', 'strike', 'blockquote',
  'list', 'bullet', 'indent',
  'link', 'image', 'video'
]

const DetailDoctorForManagerClinic = () => {
  const [dataDoctor, setDataDoctor] = useState({});
  const [dataDoctorRes, setDataDoctorRes] = useState({});
  const [form] = Form.useForm();
  const [formIntroduce] = Form.useForm();
  const [optionsClinic, setOptionsClinic] = useState([]);
  const [optionsSpecialty, setOptionsSpecialty] = useState([]);
  const [loading, setLoading] = useState(false);
  const [editInformation, setEditInformation] = useState(false);
  const [editIntroduce, setEditIntroduce] = useState(false);
  const [fileListAvatar, setFileListAvatar] = useState([]);
  const [doctorInforId, setDoctorInforId] = useState('');
  const [dataDoctorInfo, setDataDoctorInfo] = useState({})

  let { doctorId } = useParams();

  useEffect(() => {
    document.title = 'Thng tin bc s';
    const userLocalStorage = JSON.parse(localStorage.getItem('user'));
    if (userLocalStorage) getListSpecialty(userLocalStorage)
  }, [])

  useEffect(() => {
    if (doctorId) {
      getInfoDoctor(doctorId);
    }
  }, [doctorId]);

  const getInfoDoctor = async (doctorId) => {
    setLoading(true);
    try {
      const dataRes = await UserApis.getUserById(doctorId);
      if (dataRes.status === 200) {
        const { data } = dataRes
        setDataDoctor({
          id: data.id,
          firstName: data?.firstName || '',
          middleName: data?.middleName || '',
          lastName: data?.lastName || '',
          email: data?.email || '',
          gender: data?.gender || null,
          phoneNumber: data?.phoneNumber || null,
          birthday: data?.birthday || '',
          address: data?.address || '',
          role: data.role || '',
          clinic: data.clinic ? data.clinic.id : '',
          specialty: data.specialty ? data.specialty.name : '',
          avatar: data.avatar || '',
          religion: data?.religion || '',
          nation: data?.nation || '',
          identityCardNumber: data?.identityCardNumber || '',
          // avatar: data?.avatar ? [{
          //   uid: '-1',
          //   name: 'image.jpg',
          //   status: 'done',
          //   url: `http://14.225.255.59:8000/${data.avatar}`
          // }] : [],
        });
        setFileListAvatar(data?.avatar ? [{
          uid: '-1',
          name: 'image.jpg',
          status: 'done',
          url: `${baseURL}${data.avatar}`
        }] : []);

        form.setFieldsValue({
          firstName: data?.firstName || '',
          middleName: data?.middleName || '',
          lastName: data?.lastName || '',
          email: data?.email || '',
          gender: data?.gender || null,
          phoneNumber: data?.phoneNumber || null,
          birthday: data?.birthday ? moment(data?.birthday) : null,
          address: data?.address || '',
          role: data.role || '',
          clinicId: data?.clinic ? data.clinic?.id : null,
          specialtyId: data?.specialty ? data.specialty?.id : null,
          avatar: data?.avatar || null,
          religion: data?.religion || '',
          nation: data?.nation || '',
          identityCardNumber: data?.identityCardNumber || '',
        })
        setDataDoctorRes(data);
        if (data?.doctorInfor) {
          const { doctorInfor } = data;
          setDoctorInforId(data?.doctorInfor?.id || '');
          // await getInfoDoctorExtra(data?.doctorInfor || {});
          formIntroduce.setFieldsValue({
            position: doctorInfor?.position || null,
            price: doctorInfor?.price || 0,
            payment: doctorInfor?.payment || null,
            introduct: doctorInfor?.introduct || null,
            note: doctorInfor?.note || null,
            description: doctorInfor?.description || null,
          })
        }
        setLoading(false);
      }
    } catch (error) {
      console.log('error: ', error);
      setLoading(false);
    }
  }

  const getListSpecialty = async (dataUser) => {
    setLoading(true);
    try {
      const dataRes = await SpecialtyApis.getSpecialtyByClinic(dataUser.clinic.id);
      if (dataRes?.data?.data) {
        const { data } = dataRes?.data;
        const listSpecialty = data?.map(item => {
          return {
            id: item.id,
            name: item.name,
          }
        })
        setOptionsSpecialty(listSpecialty || [])
      }
    } catch (error) {
      console.log('error: ', error);
      setLoading(false);
      setOptionsSpecialty([]);
    }
  }

  const handleBack = () => {
    // const partName = location.pathname.split('/').slice(0, 3).join('/');
    // console.log('partName: ', partName);
    // history.push(partName);
    window.history.back();
  }

  const onChangeAvatar = ({ fileList: newFileList }) => {
    setFileListAvatar(newFileList);
  };

  const handleUpdateDoctor = async (value) => {
    setLoading(true);
    let newData = [];
    if (value.avatar === dataDoctor.avatar || (!value.avatar) || (value.avatar && value?.avatar?.fileList?.length === 0)) {
      const { avatar, ...resData } = value;
      newData = resData
    } else if (value?.avatar?.fileList?.length > 0) {
      const fileSize = value?.avatar?.fileList[0]?.size;
      const isLt2M = fileSize / 1024 / 1024 < 2;
      // console.log('isLt2M: ', isLt2M);
      if (!isLt2M) {
        message.error('Chn nh nh hn 2MB!');
      }
      let formData = new FormData();
      const { avatar, ...resData } = value;
      for (const item in value) {
        if (item === 'avatar') {
          formData.append('file', value[item].fileList[0]?.originFileObj);
        }
        if (!value[item]) break;

        formData.append(item, value[item]);
      }
      // formData.append('file', avatar?.fileList[0]?.originFileObj);
      newData = formData;
    }

    try {
      const dataRes = await DoctorApis.updateDoctor(newData, doctorId);
      if (dataRes.status === 200) {
        setLoading(false);
        setEditInformation(false);
        toast.success('Cp nht thng tin bc s thnh cng');
        setEditInformation(false);
      }
    } catch (error) {
      console.log('error: ', error);
      setLoading(false);
      if (error.response.data.error === 'USER_ALREADY_EXIST' && error.response.data.status === 409) {
        toast.error('Bc s  tn ti!');
        return;
      }
      toast.error('Cp nht thng tin bc s khng thnh cng!');
    }
  }

  const handleUpdateDoctorIntroduce = async (values) => {
    setLoading(true);
    if (!doctorInforId && Object.keys(dataDoctorInfo).length === 0) {
      try {
        const dataSaveDoctorInfo = await DoctorApis.createDoctorInfor({
          ...values,
          doctorId: doctorId,
        })
        if (dataSaveDoctorInfo?.status === 200) {
          toast.success('Thm gii thiu bc s thnh cng');
          setLoading(false);
          setEditIntroduce(false);
        }
      } catch (error) {
        console.log('error: ', error);
        toast.error('Thm gii thiu bc s khng thnh cng!');
        setLoading(false);
      }

    } else {
      // cCp nht thng tin gii thiu
      try {
        const dataSaveDoctorInfo = await DoctorApis.updateDoctorInfoExtra(values, doctorInforId);
        if (dataSaveDoctorInfo?.status === 200) {
          toast.success('Thay i thng tin gii thiu bc s thnh cng');
          setLoading(false);
          setEditIntroduce(false);
        }
      } catch (error) {
        console.log('error: ', error);
        toast.error('Thay i thng tin gii thiu bc s khng thnh cng!');
        setLoading(false);
      }
    }
  }

  return (
    <>
      {loading ? (
        <div className="spin">
          <Spin />
        </div>

      ) : (
        <>
          <div className='header_detail_doctor'>
            <Space>
              <BackIcon
                onClick={handleBack}
                style={{
                  cursor: 'pointer',
                  height: '100%'
                }}
              />
              <span className='name_doctor'>
                {`${dataDoctor.firstName || ''} ${dataDoctor.middleName || ''} ${dataDoctor.lastName || ''}`.trim()}
              </span>
            </Space>
          </div>
          <Tabs defaultActiveKey="doctor_information">
            <Tabs.TabPane tab="Thng tin bc s" key="doctor_information">
              <div className="doctor_information">
                <div className="header_doctor_information">
                  <div style={{ fontSize: '17px' }}>Thng tin bc s</div>
                  <div className='button_edit'>
                    {!editInformation && (
                      <Button
                        type='primary'
                        onClick={() => setEditInformation(true)}
                      >
                        Chnh sa thng tin bc s
                      </Button>
                    )}
                  </div>
                </div>

                <div className="form_doctor_information">
                  <Form
                    name='doctor'
                    onFinish={(values) => handleUpdateDoctor(values)}
                    autoComplete='off'
                    layout='vertical'
                    form={form}
                  >
                    <Row gutter={24}>
                      <Col span={6}>
                        <Form.Item
                          name={'firstName'}
                          label={<span className='txt_label'>H</span>}
                          rules={[
                            {
                              required: true,
                              message: 'H khng c  trng',
                            }
                          ]}
                        >
                          <Input
                            disabled={!editInformation}
                            size='middle'
                            className='txt_input'
                            placeholder={dataDoctor?.firstName ? 'H' : 'Khng c thng tin'} />
                        </Form.Item>
                      </Col>
                      <Col span={6}>
                        <Form.Item
                          name={'middleName'}
                          label={<span className='txt_label'>Tn m</span>}
                        >
                          <Input
                            disabled={!editInformation}
                            size='middle'
                            className='txt_input'
                            placeholder={dataDoctor?.middleName ? 'Tn m' : 'Khng c thng tin'} />
                        </Form.Item>
                      </Col>
                      <Col span={6}>
                        <Form.Item
                          name={'lastName'}
                          label={<span className='txt_label'>Tn</span>}
                          rules={[
                            {
                              required: true,
                              message: 'Tn khng c  trng',
                            }
                          ]}
                        >
                          <Input
                            disabled={!editInformation}
                            size='middle'
                            className='txt_input'
                            placeholder={dataDoctor?.lastName ? 'Tn' : 'Khng c thng tin'} />
                        </Form.Item>
                      </Col>

                      <Col span={6}>
                        <Form.Item
                          name={'avatar'}
                          label={<span className='txt_label'>nh bc s</span>}
                        // rules={[
                        //   {
                        //     required: true,
                        //     message: 'Tn chuyn khoa khng c  trng',
                        //   }
                        // ]}
                        >
                          <Upload
                            disabled={!editInformation}
                            listType="picture-card"
                            fileList={fileListAvatar}
                            beforeUpload={() => false}
                            onChange={onChangeAvatar}
                            maxCount={1}
                          >
                            <div>
                              <PlusOutlined />
                              <div style={{ marginTop: 8 }}>Upload</div>
                            </div>
                          </Upload>
                        </Form.Item>
                      </Col>

                      <Col span={6}>
                        <Form.Item
                          name={'email'}
                          label={<span className='txt_label'>Email</span>}
                          rules={[
                            { max: 255, message: 'Email ti a 255 k t!' },
                            {
                              required: true,
                              message: 'Email khng c  trng',
                            },
                            {
                              validator: (_, value) => {
                                const regex = new RegExp(/^[a-z0-9](\.?[a-z0-9]){2,}@gmail\.com$/gi);
                                if ((regex.test(value) && value)) {
                                  return Promise.resolve();
                                } else {
                                  return Promise.reject('Email khng ng nh dng');
                                }
                              },
                            },
                          ]}
                        >
                          <Input
                            disabled={!editInformation}
                            size='middle'
                            className='txt_input'
                            placeholder={dataDoctor?.email ? 'Email' : 'Khng c thng tin'} />
                        </Form.Item>
                      </Col>
                      <Col span={6}>
                        <Form.Item
                          name={'gender'}
                          label={<span className='txt_label'>Gii tnh</span>}
                          rules={[
                            {
                              required: true,
                              message: 'Gii tnh khng c  trng',
                            },
                          ]}
                        >
                          <Select
                            disabled={!editInformation}
                            style={{ width: '100%' }}
                            size='middle'
                            placeholder={dataDoctor?.gender ? 'Chn gii tnh' : 'Khng c thng tin'}
                            className='txt_input'
                          >
                            {listGender.map((item, index) => (
                              <Option key={index} value={item.key}>
                                {item.value}
                              </Option>
                            ))}
                          </Select>
                        </Form.Item>
                      </Col>
                      <Col span={6}>
                        <Form.Item
                          name={'phoneNumber'}
                          label={<span className='txt_label'>S in thoi</span>}
                          rules={[
                            {
                              max: 20,
                              message: 'S in thoi ti a 20 k t',
                            },
                          ]}
                        >
                          <Input
                            disabled={!editInformation}
                            className='txt_input'
                            size='middle'
                            placeholder={dataDoctor?.phoneNumber ? 'S in thoi' : 'Khng c thng tin'}
                          />
                        </Form.Item>
                      </Col>
                      <Col span={6}>
                        <Form.Item name={'birthday'} label={<span className='txt__label'>Ngy sinh</span>}>
                          <DatePicker
                            disabled={!editInformation}
                            picker='date'
                            showNow={false}
                            // showTime
                            placeholder={dataDoctor?.birthday ? 'Chn ngy' : 'Khng c thng tin'}
                            size={'middle'}
                            format={'DD/MM/YYYY'}
                            style={{
                              width: '100%',
                              cursor: 'pointer',
                              fontSize: '14px',
                            }}
                            className='txt_input'
                            disabledDate={(current) => moment() <= current}
                          />
                        </Form.Item>
                      </Col>

                      <Col span={8}>
                        <Form.Item
                          name={'nation'}
                          label={<span className='txt_label'>Quc gia</span>}
                        >
                          <Input
                            disabled={!editInformation}
                            className='txt_input'
                            size='middle'
                            placeholder={dataDoctor?.nation ? 'Quc gia' : 'Khng c thng tin'}
                          />
                        </Form.Item>
                      </Col>
                      <Col span={8}>
                        <Form.Item
                          name={'religion'}
                          label={<span className='txt_label'>Tn gio</span>}
                        >
                          <Input
                            disabled={!editInformation}
                            className='txt_input'
                            size='middle'
                            placeholder={dataDoctor?.religion ? 'Tn gio' : 'Khng c thng tin'}
                          />
                        </Form.Item>
                      </Col>
                      <Col span={8}>
                        <Form.Item
                          name={'identityCardNumber'}
                          label={<span className='txt_label'>CCCD/CTM</span>}
                        >
                          <Input
                            disabled={!editInformation}
                            className='txt_input'
                            size='middle'
                            placeholder={dataDoctor?.identityCardNumber ? 'CCCD/CMT' : 'Khng c thng tin'}
                          />
                        </Form.Item>
                      </Col>

                      <Col span={10}>
                        <Form.Item
                          name={'role'}
                          label={<span className='txt_label'>Vai tr</span>}
                          rules={[
                            {
                              required: true,
                              message: 'Vai tr khng c  trng',
                            },
                          ]}
                        >
                          <Select
                            disabled={!editInformation}
                            style={{ width: '100%' }}
                            size='middle'
                            placeholder={dataDoctor?.role ? 'Chn vai tr' : 'Khng c thng tin'}
                            className='txt_input'
                          >
                            {listRole.map((item, index) => (
                              <Option key={index} value={item.key}>
                                {item.value}
                              </Option>
                            ))}
                          </Select>
                        </Form.Item>
                      </Col>

                      <Col span={14}>
                        <Form.Item
                          name={'specialtyId'}
                          label={<span className='txt_label'>Chuyn khoa</span>}
                        // rules={[
                        //   {
                        //     required: true,
                        //     message: 'Gii tnh khng c  trng',
                        //   },
                        // ]}
                        >
                          <Select
                            disabled={!editInformation}
                            showSearch
                            style={{ width: '100%' }}
                            size='middle'
                            placeholder={dataDoctor?.specialtyId ? 'Chn chuyn khoa' : 'Khng c thng tin'}
                            className='txt_input'
                            filterOption={(input, option) =>
                              option?.label !== null && option?.label?.toLowerCase().includes(input.trim().toLowerCase())
                            }
                          >
                            {optionsSpecialty.length && optionsSpecialty.map((item) => (
                              <Option key={item.id} value={item.id}>
                                {item.name}
                              </Option>
                            ))}
                          </Select>
                        </Form.Item>
                      </Col>
                    </Row>
                    {editInformation && (
                      <Col span={24} style={{ textAlign: 'center' }}>
                        <Button className='btn_cancel' danger size='middle' onClick={() => setEditInformation(false)}>
                          Hy chnh sa
                        </Button>
                        <Button className='btn_add' size='middle' htmlType='submit' type='primary'>
                          Cp nht
                        </Button>
                      </Col>
                    )}
                  </Form>
                </div>
              </div>

            </Tabs.TabPane>
            <Tabs.TabPane tab="Gii thiu v bc s" key="doctor_introduce" disabled={dataDoctor && dataDoctor.role === 'MANAGER_CLINIC'}>
              <div className="doctor_introduce">
                <div className="header_doctor_introduce">
                  <div style={{ fontSize: '17px' }}>Gii thiu chi tit bc s</div>
                  <div className='button_edit'>
                    {!editIntroduce && (
                      <Button
                        type='primary'
                        onClick={() => setEditIntroduce(true)}
                      >
                        {!doctorInforId ? 'Thm gii thiu bc s' : 'Chnh sa thng tin'}
                      </Button>
                    )}
                  </div>
                </div>
                <div className="form_doctor_introduce">
                  <Form
                    name='doctor_introduce'
                    onFinish={(values) => handleUpdateDoctorIntroduce(values)}
                    autoComplete='off'
                    layout='vertical'
                    form={formIntroduce}
                  >
                    <Row gutter={24}>
                      <Col span={8}>
                        <Form.Item
                          name={'position'}
                          label={<span className='txt_label'>Chc danh</span>}
                        >
                          <Select
                            disabled={!editIntroduce}
                            style={{ width: '100%' }}
                            size='middle'
                            placeholder={true ? 'Chn chc danh' : 'Khng c thng tin'}
                            className='txt_input'
                          >
                            {listPositon.map((item, index) => (
                              <Option key={index} value={item.key}>
                                {item.value}
                              </Option>
                            ))}
                          </Select>
                        </Form.Item>
                      </Col>
                      <Col span={8}>
                        <Form.Item
                          name={'price'}
                          label={<span className='txt_label'>Gi khm</span>}
                        >
                          <InputNumber
                            defaultValue={0}
                            style={{ width: '100%' }}
                            formatter={(value) => `$ ${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ".").replace(/\.(?=\d{0,2}$)/g, ",")}
                            parser={(value) => value.replace(/\$\s?|(\.*)/g, "").replace(/(\,{1})/g, ".")}
                            placeholder={true ? 'Gi khm' : 'Khng c thng tin'}
                            disabled={!editIntroduce}
                            className='txt_input'
                            size='middle'
                          />

                        </Form.Item>
                      </Col>
                      <Col span={8}>
                        <Form.Item
                          name={'payment'}
                          label={<span className='txt_label'>Phng thc thanh ton</span>}
                        >
                          <Select
                            disabled={!editIntroduce}
                            style={{ width: '100%' }}
                            size='middle'
                            placeholder={true ? 'Chn phng thc thanh ton' : 'Khng c thng tin'}
                            className='txt_input'
                          >
                            {listPayment.map((item, index) => (
                              <Option key={index} value={item.key}>
                                {item.value}
                              </Option>
                            ))}
                          </Select>
                        </Form.Item>
                      </Col>
                      <Col span={12}>
                        <Form.Item
                          name={'introduct'}
                          label={<span className='txt_label'>Gii thiu</span>}
                        >
                          <TextArea
                            disabled={!editIntroduce}
                            rows={4}
                            placeholder='Gii thiu bc s'
                          />
                        </Form.Item>
                      </Col>
                      <Col span={12}>
                        <Form.Item
                          name={'note'}
                          label={<span className='txt_label'>Ghi ch</span>}
                        >
                          <TextArea
                            disabled={!editIntroduce}
                            rows={4}
                            placeholder='Ghi ch'
                          />
                        </Form.Item>
                      </Col>

                      <Col span={24}>
                        <Form.Item
                          name={'description'}
                          label={<span className='txt_label'>M t bc s</span>}
                        >
                          <ReactQuill
                            readOnly={!editIntroduce}
                            className={!editIntroduce ? 'react_quill_disable' : ''}
                            theme="snow"
                            placeholder="M t"
                            modules={modulesQill}
                            formats={formats}
                            // bounds={'#root'}
                            style={{ height: "500px" }}
                          />
                        </Form.Item>
                      </Col>

                      {editIntroduce && doctorInforId && (
                        <div style={{ textAlign: 'center', marginTop: '30px', width: '100%' }}>
                          <Col span={24} style={{ textAlign: 'center', marginTop: '20px !important' }}>
                            <Button className='btn_cancel' danger size='middle' onClick={() => setEditIntroduce(false)}>
                              Hy chnh sa
                            </Button>
                            <Button className='btn_add' size='middle' htmlType='submit' type='primary'>
                              Cp nht
                            </Button>
                          </Col>
                        </div>
                      )}

                      {editIntroduce && !doctorInforId && (
                        <div style={{ textAlign: 'center', marginTop: '30px', width: '100%' }}>
                          <Col span={24} >
                            <Button
                              className='btn_cancel'
                              danger size='middle'
                              onClick={() => setEditIntroduce(false)}
                            >
                              Hy
                            </Button>
                            <Button className='btn_add' size='middle' htmlType='submit' type='primary'>
                              Thm
                            </Button>
                          </Col>
                        </div>
                      )}

                    </Row>
                  </Form>
                </div>
              </div>
            </Tabs.TabPane>
          </Tabs>
        </>
      )}
    </>
  )
}

export default DetailDoctorForManagerClinic;