import { Button, Col, DatePicker, Form, Input, Modal, Spin, Row, Select } from 'antd';
import moment from 'moment';
import React, { useEffect, useState } from 'react';
import './AddEditUser.scss';
import CustomerApis from '../../../apis/Customer';
import { toast } from 'react-toastify';

const { Option } = Select;
const listGender = [
  {
    key: 'MALE',
    value: 'Nam'
  },
  {
    key: 'FEMALE',
    value: 'N'
  },
  {
    key: 'OTHER',
    value: 'Khc'
  }
]
const AddEditUser = ({
  isShowModal,
  type,
  handleCancelModal, // function cancel
  dataCustomer, // data update from dertail customer
}) => {
  const [loading, setLoading] = useState(false);
  const [form] = Form.useForm();

  useEffect(() => {
    if (type === 'update' && dataCustomer) {
      form.setFieldsValue({
        ...dataCustomer,
        birthday: dataCustomer.birthday ? moment(dataCustomer.birthday) : null,

      })
    }
  }, [dataCustomer])

  const handleAddEditCustomer = async (value) => {
    if (type === 'create') {
      await addNewCustomer(value);
    } else if (type === 'update' && dataCustomer) {
      await updateCustomer(value);
    }
  }

  const addNewCustomer = async (value) => {
    try {
      setLoading(true)
      const dataRes = await CustomerApis.createCustomer({
        ...value,
        role: 'USER',
      })
      if (dataRes.status === 200) {
        setLoading(false);
        toast.success('Thm khch hng thnh cng');
        handleCancelModal();
      }
    } catch (error) {
      console.log('error: ', error);
      setLoading(false);
      if (error.response.data.error === 'USER_ALREADY_EXIST' && error.response.data.status === 409) {
        toast.error('Khch hng  tn ti!');
      }
      toast.error('Thm khch hng khng thnh cng!');
    }
  }

  const updateCustomer = async (value) => {
    try {
      setLoading(true)
      const dataRes = await CustomerApis.updateCustomer(dataCustomer.id, { ...value });
      if (dataRes.status === 200) {
        setLoading(false);
        toast.success('Sa thng tin khch hng thnh cng');
        handleCancelModal();
      }
    } catch (error) {
      console.log('error: ', error);
      setLoading(false);
      if (error.response.data.error === 'USER_ALREADY_EXIST' && error.response.data.status === 409) {
        toast.error('Khch hng  tn ti!');
        return;
      }
      toast.error('Sa thng tin khch hng khng thnh cng');
    }
  }

  return (
    <Modal
      className='create_modal'
      title={
        <>
          {type == 'create' ? (
            <div>Thm khch hng</div>
          ) : (
            <div>Sa thng tin khch hng</div>
          )}
        </>
      }
      open={isShowModal}
      onCancel={() => {
        !loading && handleCancelModal()
      }}
      width={700}
      footer={false}
    >
      <Spin spinning={loading}>
        <Form
          name='user'
          onFinish={(values) => handleAddEditCustomer(values)}
          autoComplete='off'
          layout='vertical'
          form={form}
        >
          <Row gutter={24}>
            <Col span={8}>
              <Form.Item
                name={'firstName'}
                label={<span className='txt_label'>H</span>}
                rules={[
                  {
                    required: true,
                    message: 'H khng c  trng',
                  }
                ]}
              >
                <Input
                  size='middle'
                  className='txt_input'
                  placeholder={dataCustomer?.firstName ? 'H' : 'Khng c thng tin'}
                />
              </Form.Item>
            </Col>
            <Col span={8}>
              <Form.Item
                name={'middleName'}
                label={<span className='txt_label'>Tn m</span>}
              // rules={[
              //   {
              //     required,
              //     message: 'H khng c  trng',
              //   }
              // ]}
              >
                <Input
                  size='middle'
                  className='txt_input'
                  placeholder={dataCustomer?.middleName ? 'Tn m' : 'Khng c thng tin'}
                />
              </Form.Item>
            </Col>
            <Col span={8}>
              <Form.Item
                name={'lastName'}
                label={<span className='txt_label'>Tn</span>}
                rules={[
                  {
                    required: true,
                    message: 'Tn khng c  trng',
                  }
                ]}
              >
                <Input
                  size='middle'
                  className='txt_input'
                  placeholder={dataCustomer?.lastName ? 'Tn' : 'Khng c thng tin'}
                />
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item
                name={'email'}
                label={<span className='txt_label'>Email</span>}
                rules={[
                  { max: 255, message: 'Email ti a 255 k t!' },
                  {
                    required: true,
                    message: 'Email khng c  trng',
                  },
                  {
                    validator: (_, value) => {
                      const regex = new RegExp(/^[a-z0-9](\.?[a-z0-9]){2,}@gmail\.com$/gi);
                      if ((regex.test(value) && value)) {
                        return Promise.resolve();
                      } else {
                        return Promise.reject('Email khng ng nh dng');
                      }
                    },
                  },
                ]}
              >
                <Input
                  size='middle'
                  className='txt_input'
                  placeholder={dataCustomer?.email ? 'Email' : 'Khng c thng tin'}
                />
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item
                name={'gender'}
                label={<span className='txt_label'>Gii tnh</span>}
                rules={[
                  {
                    required: true,
                    message: 'Gii tnh khng c  trng',
                  },
                ]}
              >
                <Select
                  style={{ width: '100%' }}
                  size='middle'
                  placeholder={dataCustomer?.gender ? 'Chn gii tnh' : 'Khng c thng tin'}
                  className='txt_input'
                >
                  {listGender.map((item, index) => (
                    <Option key={index} value={item.key}>
                      {item.value}
                    </Option>
                  ))}
                </Select>
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item
                name={'phoneNumber'}
                label={<span className='txt_label'>S in thoi</span>}
                rules={[
                  {
                    max: 20,
                    message: 'S in thoi ti a 20 k t',
                  },
                ]}
              >
                <Input
                  className='txt_input'
                  size='middle'
                  placeholder={dataCustomer?.phoneNumber ? 'S in thoi' : 'Khng c thng tin'}
                />
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item name={'birthday'} label={<span className='txt__label'>Ngy sinh</span>}>
                <DatePicker
                  picker='date'
                  showNow={false}
                  // showTime
                  placeholder={dataCustomer?.birthday ? 'Chn ngy' : 'Khng c thng tin'}
                  size={'middle'}
                  format={'DD/MM/YYYY'}
                  style={{
                    width: '100%',
                    cursor: 'pointer',
                    fontSize: '14px',
                  }}
                  className='txt_input'
                  disabledDate={(current) => moment() <= current}
                />
              </Form.Item>
            </Col>
            <Col span={24}>
              <Form.Item
                name={'address'}
                label={<span className='txt_label'>a ch</span>}
                rules={[
                  {
                    max: 255,
                    message: 'a ch ti a 255 k t',
                  },
                ]}
              >
                <Input
                  className='txt_input'
                  size='middle'
                  placeholder={dataCustomer?.address ? 'a ch' : 'Khng c thng tin'}
                />
              </Form.Item>
            </Col>
          </Row>
          <Col span={24} style={{ textAlign: 'center' }}>
            <Button className='btn_cancel' danger size='middle' onClick={() => handleCancelModal()}>
              Hy
            </Button>
            {type === 'create' ? (
              <Button className='btn_add' size='middle' htmlType='submit' type='primary'>
                Thm khch hng
              </Button>
            ) : (
              <Button className='btn_add' size='middle' htmlType='submit' type='primary'>
                Lu thay i
              </Button>
            )}
          </Col>
        </Form>
      </Spin>
    </Modal>
  )
}

export default AddEditUser;