import { Button, Col, DatePicker, Form, Input, Modal, Spin, Row, Select } from 'antd';
import moment from 'moment';
import React, { useEffect, useState } from 'react';
import './CreateClinic.scss';
import { toast } from 'react-toastify';
import ClinicApis from '../../../apis/Clinic';
import SpecialtyApis from '../../../apis/Specialty';
import DoctorApis from '../../../apis/Doctor';

const { Option } = Select;

const CreateClinic = ({
  isShowModal,
  handleCancelModal, // function cancel
}) => {
  const [loading, setLoading] = useState(false);
  const [optionsSpecialty, setOptionsSpecialty] = useState([]);
  const [form] = Form.useForm();

  useEffect(() => {

    !!isShowModal && getListSpecialty();
  }, [isShowModal])

  const handleAddNewClinic = async (value) => {
    console.log('value: ', value);
    try {
      setLoading(true)
      const dataRes = await ClinicApis.createClinic({
        ...value,
        specialties: JSON.stringify(value.specialties),
      })
      // 
      if (dataRes.status === 200) {
        setLoading(false);
        toast.success('Thm phng khm thnh cng');
        handleCancelModal();
        form.setFieldsValue({
          name: '',
          emai: '',
          phone: '',
          email: '',
          address: '',
          province: '',
          specialties: undefined
        })
      }
    } catch (error) {
      console.log('error: ', error);
      setLoading(false);
      if (error.response.data.error === 'CLINIC_ALREADY_EXIST' && error.response.data.status === 409) {
        toast.error('Phng khm  tn ti!');
      }
      toast.error('Thm phng khm khng thnh cng!');
    }
  }

  const getListSpecialty = async (value) => {
    try {
      const dataSpecialty = await SpecialtyApis.getListSpecialty({
        page: 1,
        take: 100,
      });
      if (dataSpecialty?.data?.data?.length > 0) {
        const { data } = dataSpecialty?.data;
        const listSpecialty = data?.map(item => {
          return {
            id: item.id,
            name: item.name,
          }
        })

        setOptionsSpecialty(listSpecialty || [])
      } else if (dataSpecialty?.data?.data?.length === 0) {
        setOptionsSpecialty([]);
        form.setFieldsValue({
          specialtyId: undefined
        })
      }
    } catch (error) {
      // console.log('error: ', error);
      setOptionsSpecialty([]);
      form.setFieldsValue({
        specialtyId: undefined
      })
    }
  }


  return (
    <Modal
      className='create_modal'
      title={
        <>
          <div>Thm phng khm</div>
        </>
      }
      visible={isShowModal}
      onCancel={() => {
        !loading && handleCancelModal()
      }}
      width={700}
      footer={false}
    >
      <Spin spinning={loading}>
        <Form
          name='user'
          onFinish={(values) => handleAddNewClinic(values)}
          autoComplete='off'
          layout='vertical'
          form={form}
        >
          <Row gutter={24}>
            <Col span={24}>
              <Form.Item
                name={'name'}
                label={<span className='txt_label'>Tn phng khm</span>}
                rules={[
                  {
                    required: true,
                    message: 'Tn phng khm khng c  trng',
                  }
                ]}
              >
                <Input
                  size='middle'
                  className='txt_input'
                  placeholder={'Tn phng khm'} />
              </Form.Item>
            </Col>

            <Col span={12}>
              <Form.Item
                name={'email'}
                label={<span className='txt_label'>Email</span>}
                rules={[
                  { max: 255, message: 'Email ti a 255 k t!' },
                  {
                    required: true,
                    message: 'Email khng c  trng',
                  },
                  {
                    validator: (_, value) => {
                      const regex = new RegExp(/^[a-z0-9](\.?[a-z0-9]){2,}@gmail\.com$/gi);
                      if ((regex.test(value) && value)) {
                        return Promise.resolve();
                      } else {
                        return Promise.reject('Email khng ng nh dng');
                      }
                    },
                  },
                ]}
              >
                <Input
                  size='middle'
                  className='txt_input'
                  placeholder={'Email'} />
              </Form.Item>
            </Col>

            <Col span={12}>
              <Form.Item
                name={'phone'}
                label={<span className='txt_label'>S in thoi</span>}
                rules={[
                  {
                    max: 20,
                    message: 'S in thoi ti a 20 k t',
                  },
                ]}
              >
                <Input
                  className='txt_input'
                  size='middle'
                  placeholder={true ? 'S in thoi' : 'Khng c thng tin'}
                />
              </Form.Item>
            </Col>
            <Col span={16}>
              <Form.Item
                name={'address'}
                label={<span className='txt_label'>a ch</span>}
              // rules={[
              //   {
              //     max: 20,
              //     message: 'S in thoi ti a 20 k t',
              //   },
              // ]}
              >
                <Input
                  className='txt_input'
                  size='middle'
                  placeholder={true ? 'a ch' : 'Khng c thng tin'}
                />
              </Form.Item>
            </Col>
            <Col span={8}>
              <Form.Item
                name={'province'}
                label={<span className='txt_label'>Tnh/Thnh ph</span>}
                rules={[
                  {
                    required: true,
                    message: 'Tnh/Thnh ph khng c  trng',
                  },
                ]}
              >
                <Input
                  className='txt_input'
                  size='middle'
                  placeholder={true ? 'Tnh/Thnh ph' : 'Khng c thng tin'}
                />
              </Form.Item>
            </Col>


            <Col span={24}>
              <Form.Item
                name={'specialties'}
                label={<span className='txt_label'>Chuyn khoa</span>}
              // rules={[
              //   {
              //     required: true,
              //     message: 'Gii tnh khng c  trng',
              //   },
              // ]}
              >
                <Select
                  mode='multiple'
                  allowClear
                  showArrow
                  style={{ width: '100%' }}
                  size='middle'
                  placeholder={true ? 'Chn chuyn khoa' : 'Khng c thng tin'}
                  className='txt_input'
                  // defaultValue={}
                  filterOption={(input, option) =>
                    option?.children !== null && option?.children?.toLowerCase().includes(input.trim().toLowerCase())
                  }
                >
                  {optionsSpecialty.length && optionsSpecialty.map((item) => (
                    <Option key={item.id} value={item.id}>
                      {item.name}
                    </Option>
                  ))}
                </Select>
              </Form.Item>
            </Col>
          </Row>
          <Col span={24} style={{ textAlign: 'center' }}>
            <Button className='btn_cancel' danger size='middle' onClick={() => handleCancelModal()}>
              Hy
            </Button>
            <Button className='btn_add' size='middle' htmlType='submit' type='primary'>
              Thm phng khm
            </Button>
          </Col>
        </Form>
      </Spin>
    </Modal >
  )
}

export default CreateClinic;