import { Button, Col, DatePicker, Form, Input, Modal, Spin, Row, Select } from 'antd';
import moment from 'moment';
import React, { useEffect, useState } from 'react';
import './CreateStaff.scss';
import { toast } from 'react-toastify';
import CareStaffApis from '../../../apis/CareStaff';

const { Option } = Select;
const listGender = [
  {
    key: 'MALE',
    value: 'Nam'
  },
  {
    key: 'FEMALE',
    value: 'N'
  },
  {
    key: 'ORTHER',
    value: 'Khc'
  }
]
const CreateStaff = ({
  isShowModal,
  type,
  detailStaff,
  handleCancelModal, // function cancel
}) => {
  const [loading, setLoading] = useState(false);
  const [form] = Form.useForm();

  console.log('detailStaff: ', detailStaff);

  useEffect(() => {
    if (type === 'update' && !!detailStaff) {
      form.setFieldsValue({
        firstName: detailStaff?.firstName || '',
        middleName: detailStaff?.middleName || '',
        lastName: detailStaff?.lastName || '',
        email: detailStaff?.email || '',
        phoneNumber: detailStaff?.phoneNumber || null,
        gender: detailStaff?.gender || null,
        birthday: detailStaff?.birthday ? moment(detailStaff.birthday) : null,
        address: detailStaff?.address || '',
      })
    }
  }, [detailStaff])

  const handleSubmitStaff = async (value) => {
    if (type === 'create') {
      try {
        setLoading(true)
        const dataRes = await CareStaffApis.createCareStaff({
          ...value,
          role: 'ADMIN',
        })
        // 
        if (dataRes.status === 200) {
          setLoading(false);
          toast.success('Thm nhn vin thnh cng');
          handleCancelModal();
          form.setFieldsValue({
            firstName: '',
            middleName: '',
            lastName: '',
            email: '',
            phoneNumber: '',
            gender: undefined,
            birthday: undefined,
            address: '',
          })
        }
      } catch (error) {
        setLoading(false);
        if (error.response.data.error === 'USER_ALREADY_EXIST' && error.response.data.status === 409) {
          toast.error('Nhn vin  tn ti!');
        }
        toast.error('Li');
      }
    } else if (type === 'update' && !!detailStaff) {
      try {
        setLoading(true);
        const dataRes = await CareStaffApis.updateCareStaff(value, detailStaff.id)
        // 
        if (dataRes.status === 200) {
          setLoading(false);
          toast.success('Cp nht thng tin nhn vin thnh cng');
          handleCancelModal();
          form.setFieldsValue({
            firstName: '',
            middleName: '',
            lastName: '',
            email: '',
            phoneNumber: '',
            gender: undefined,
            birthday: undefined,
            address: '',
          })
        }
      } catch (error) {
        setLoading(false);
        if (error.response.data.error === 'USER_ALREADY_EXIST' && error.response.data.status === 409) {
          toast.error('Nhn vin  tn ti!');
          return;
        }
        toast.error('Cp nht thng tin nhn vin khng thnh cng!');
      }
    }
  }

  return (
    <Modal
      className='create_modal'
      title={
        <>
          {type == 'create' ? (
            <div>Thm nhn vin</div>
          ) : (
            <div>Sa thng tin nhn vin</div>
          )}
        </>
      }
      visible={isShowModal}
      onCancel={() => {
        !loading && handleCancelModal()
      }}
      width={700}
      footer={false}
    >
      <Spin spinning={loading}>
        <Form
          name='user'
          onFinish={(values) => handleSubmitStaff(values)}
          autoComplete='off'
          layout='vertical'
          form={form}
        >
          <Row gutter={24}>
            <Col span={8}>
              <Form.Item
                name={'firstName'}
                label={<span className='txt_label'>H</span>}
                rules={[
                  {
                    required: true,
                    message: 'H khng c  trng',
                  }
                ]}
              >
                <Input
                  size='middle'
                  className='txt_input'
                  placeholder={'H'} />
              </Form.Item>
            </Col>
            <Col span={8}>
              <Form.Item
                name={'middleName'}
                label={<span className='txt_label'>Tn m</span>}
              // rules={[
              //   {
              //     required,
              //     message: 'H khng c  trng',
              //   }
              // ]}
              >
                <Input
                  size='middle'
                  className='txt_input'
                  placeholder={'Tn m'} />
              </Form.Item>
            </Col>
            <Col span={8}>
              <Form.Item
                name={'lastName'}
                label={<span className='txt_label'>Tn</span>}
                rules={[
                  {
                    required: true,
                    message: 'Tn khng c  trng',
                  }
                ]}
              >
                <Input
                  size='middle'
                  className='txt_input'
                  placeholder={'Tn'} />
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item
                name={'email'}
                label={<span className='txt_label'>Email</span>}
                rules={[
                  { max: 255, message: 'Email ti a 255 k t!' },
                  {
                    required: true,
                    message: 'Email khng c  trng',
                  },
                  {
                    validator: (_, value) => {
                      const regex = new RegExp(/^[a-z0-9](\.?[a-z0-9]){2,}@gmail\.com$/gi);
                      if ((regex.test(value) && value)) {
                        return Promise.resolve();
                      } else {
                        return Promise.reject('Email khng ng nh dng');
                      }
                    },
                  },
                ]}
              >
                <Input
                  size='middle'
                  className='txt_input'
                  placeholder={'Email'} />
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item
                name={'gender'}
                label={<span className='txt_label'>Gii tnh</span>}
                rules={[
                  {
                    required: true,
                    message: 'Gii tnh khng c  trng',
                  },
                ]}
              >
                <Select
                  style={{ width: '100%' }}
                  size='middle'
                  placeholder={true ? 'Chn gii tnh' : 'Khng c thng tin'}
                  className='txt_input'
                >
                  {listGender.map((item, index) => (
                    <Option key={index} value={item.key}>
                      {item.value}
                    </Option>
                  ))}
                </Select>
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item
                name={'phoneNumber'}
                label={<span className='txt_label'>S in thoi</span>}
                rules={[
                  {
                    max: 20,
                    message: 'S in thoi ti a 20 k t',
                  },
                ]}
              >
                <Input
                  className='txt_input'
                  size='middle'
                  placeholder={true ? 'S in thoi' : 'Khng c thng tin'}
                />
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item name={'birthday'} label={<span className='txt__label'>Ngy sinh</span>}>
                <DatePicker
                  picker='date'
                  showNow={false}
                  // showTime
                  placeholder={true ? 'Chn ngy' : 'Khng c thng tin'}
                  size={'middle'}
                  format={'DD/MM/YYYY'}
                  style={{
                    width: '100%',
                    cursor: 'pointer',
                    fontSize: '14px',
                  }}
                  className='txt_input'
                  disabledDate={(current) => moment() <= current}
                />
              </Form.Item>
            </Col>
            <Col span={24}>
              <Form.Item
                name={'address'}
                label={<span className='txt_label'>a ch</span>}
                rules={[
                  {
                    max: 255,
                    message: 'a ch ti a 255 k t',
                  },
                ]}
              >
                <Input
                  className='txt_input'
                  size='middle'
                  placeholder={true ? 'a ch' : 'Khng c thng tin'}
                />
              </Form.Item>
            </Col>
          </Row>
          <Col span={24} style={{ textAlign: 'center' }}>
            <Button className='btn_cancel' danger size='middle' onClick={() => handleCancelModal()}>
              Hy
            </Button>
            {type === 'create' ? (
              <Button className='btn_add' size='middle' htmlType='submit' type='primary'>
                Thm nhn vin
              </Button>
            ) : (
              <Button className='btn_add' size='middle' htmlType='submit' type='primary'>
                Lu thay i
              </Button>
            )}
          </Col>
        </Form>
      </Spin>
    </Modal>
  )
}

export default CreateStaff;