import { LockOutlined, PlusCircleOutlined, SearchOutlined, UnlockOutlined } from '@ant-design/icons';
import { Alert, Button, Input, Modal, Space, Switch, Table } from 'antd';
import moment from 'moment';
import React, { useEffect, useRef, useState } from 'react';
import { toast } from 'react-toastify';
import ClinicApis from '../../apis/Clinic';
import FilterObjDropDown from '../../components/Filter/FilterObjDropDown';
import FilterIcon from '../../components/Icon/Doctor/FilterIcon';
import Stroke from '../../components/Icon/CareStaff/Stoke';
import CreateClinic from './components/CreateClinic';
import './index.scss';
import { Link } from 'react-router-dom';


const listStatus = [
  {
    id: 0,
    name: 'Kho'
  },
  {
    id: 1,
    name: 'Kch hot'
  },
]

const Clinic = () => {
  const [selectedRowKeys, setSelectedRowKeys] = useState([]);
  const [search, setSearch] = useState('');
  const typingSearch = useRef(null);
  const [pagination, setPagination] = useState({
    page: 1,
    pageSize: 10,
  });
  const [isModalCreate, setModalCreate] = useState(false);
  const [showFilter, setShowFilter] = useState(false);
  const [loading, setLoading] = useState(false);
  const [listClinic, setListClinic] = useState([]);
  const [showBtn, setShowBtn] = useState([]);
  const [checkedList, setCheckedList] = useState({
    active: [],
  })
  const [dataResponse, setDataResponse] = useState({});
  const [isShowModalDelete, setShowModalDelete] = useState(false);

  useEffect(() => {
    const checkShow = [];
    selectedRowKeys.forEach((item) => {
      const label = listClinic.find((elm) => elm.id === item);
      if (label) checkShow.push(label);
    });
    const btnArray = [];
    if (checkShow.some((item) => item.active === true)) {
      btnArray.push(
        <Button
          className='btn_active'
          icon={<LockOutlined style={{ transform: 'translateY(-1px)' }} />}
          onClick={() => {
            handleChangeActive({
              clinicIds: selectedRowKeys,
              active: false
            })
          }}
        >
          <span className='ml_8'>Kho tt c</span>
        </Button>,
      );
    }
    if (checkShow.some((item) => item.active === false)) {
      btnArray.push(
        <Button
          className='btn_active'
          icon={<UnlockOutlined style={{ transform: 'translateY(-1px)' }} />}
          onClick={() => {
            handleChangeActive({
              clinicIds: selectedRowKeys,
              active: true
            })
          }}
        >
          <span className='ml_8'>M tt c</span>
        </Button>,
      );
    }

    setShowBtn([...btnArray]);
  }, [selectedRowKeys]);

  useEffect(() => {
    if (!isShowModalDelete && !isModalCreate)
      getListClinic();
  }, [checkedList, pagination, search, isShowModalDelete, isModalCreate])

  const getListClinic = async () => {
    try {
      setLoading(true);
      const dataRes = await ClinicApis.getClinics({
        page: pagination.page,
        take: pagination.pageSize,
        active: checkedList.active || undefined,
        q: search || undefined,
      })
      if (dataRes?.data?.data) {
        const { data } = dataRes?.data;
        const listClinic = data.map(item => {
          return {
            id: item.id,
            active: item.active,
            name: item.name || '',
            email: item.email || '',
            phone: item.phone || '',
            address: item.address || '',
            createdDate: item.createdDate ? moment(item.createdDate).format('DD/MM/YYYY') : '',
          }
        })
        setListClinic(listClinic || []);
        setDataResponse(dataRes?.data ? dataRes.data : {});
        setLoading(false);
      }
    } catch (error) {
      console.log('error: ', error);
      setLoading(false);
    }
  }

  const handleChangeActive = async ({ clinicIds, active }) => {
    try {
      const dataRes = await ClinicApis.changeActive({ clinicIds, active });
      if (dataRes?.status === 200 && dataRes.data === true) {
        toast.success('Thay I trng thi thnh cng')
        getListClinic();
        setSelectedRowKeys([]);
      }
    } catch (error) {
      console.log('error: ', error);
      toast.error('Thay i trng thi khng thnh cng!')
    }
  }
  const columns = [
    {

      title: 'Trng thi',
      dataIndex: 'status',
      key: 'status',
      width: 50,
      align: 'center',
      render: (_, record) => (
        <Switch
          checked={record?.active === true}
          onChange={() =>
            handleChangeActive({
              clinicIds: [record.id],
              active: record.active === true ? false : true,
            })
          }
        />
      ),
      fixed: true,
    },
    {
      title: 'Tn phng khm',
      dataIndex: 'name',
      key: 'name',
      ellipsis: true,
      width: 100,
      fixed: true,
      render: (_, record) => (
        <div
          style={{
            cursor: 'pointer',
          }}
        >
          <Link to={location => `${location.pathname}/chi-tiet/${record.id}`}>{record.name}</Link>
        </div>
      )
    },
    {
      title: 'Email',
      dataIndex: 'email',
      key: 'email',
      ellipsis: true,
      width: 100,
    },
    {
      title: 'S in thoi',
      dataIndex: 'phone',
      key: 'phone',
      ellipsis: true,
      width: 100,
    },
    {
      title: 'a ch',
      dataIndex: 'address',
      key: 'address',
      ellipsis: true,
      width: 100,
    },
    {
      title: 'Ngy tham gia',
      dataIndex: 'createdDate',
      key: 'createdDate',
      ellipsis: true,
      width: 100,
    }
  ]

  const onSelectChange = (newSelectedRowKeys) => {
    setSelectedRowKeys(newSelectedRowKeys);
  };

  const handleCheck = (key, value) => {
    setCheckedList({
      ...checkedList,
      [key]: value,
    });
  };

  const handleCheckAll = (key, value) => {
    setCheckedList({
      ...checkedList,
      [key]: value,
    });
  };

  const handleSearch = (e) => {
    if (typingSearch.current) {
      clearTimeout(typingSearch.current);
    }
    typingSearch.current = setTimeout(() => {
      setSearch(e.target.value);
    }, 500)
  }

  const handleDeleteClinic = async () => {
    try {
      const dataRes = await ClinicApis.deleteClinic({
        clinicIds: selectedRowKeys,
      })
      if (dataRes?.data === true && dataRes.status === 200) {
        toast.success('Xo phng khm thnh cng');
        setShowModalDelete(false);
        setSelectedRowKeys([]);
      }
    } catch (error) {
      console.log('error: ', error);
      toast.error('Xo phng khm khng thnh cng!');
    }
  }

  return (
    <div>
      <h1>Danh sch phng khm</h1>

      <div className="header_clinic">
        <Space>
          <Button
            id={'btn__filter'}
            icon={
              <FilterIcon
                style={{
                  transform: 'translateY(2px)',
                  fill: `${showFilter ? '#3863EF' : 'none'}`,
                }}
              />
            }
            onClick={() => setShowFilter(!showFilter)}
          />
          <Input
            className='search_clinic'
            size="large"
            placeholder="Tm kim"
            suffix={<SearchOutlined />}
            onChange={(e) => handleSearch(e)}
          />
        </Space>

        <div className='list_button'>
          <Button
            className='button'
            size="large"
            type="primary"
            icon={<PlusCircleOutlined />}
            onClick={() => setModalCreate(true)}
          >
            Thm mi
          </Button>
        </div>
      </div>

      {/* B lc */}
      {showFilter && (
        <Space
          size={24}
          style={{ marginTop: '10px', marginBottom: '20px', color: 'rgba(17, 17, 17, 0.45)' }}
        >
          <FilterObjDropDown
            displayName={'Trng thi'}
            placeholder={'Trng thi'}
            placeholderSearch={false}
            plainOptions={listStatus || []}
            checkedList={checkedList.active}
            keyFilter={'active'}
            handleCheckAll={handleCheckAll}
            handleCheck={handleCheck}
          />
        </Space>
      )}

      {selectedRowKeys.length > 0 && (
        <Alert
          className='fontSizeAlert'
          message={
            <div>
              <Space>
                <span> chn: {selectedRowKeys.length}</span>

                {showBtn}

                <Button
                  className='btn_active'
                  icon={<Stroke className='transformY_2' />}
                  onClick={() => setShowModalDelete(true)}
                >
                  <span className='ml_8'>Xa</span>
                </Button>
              </Space>
            </div>
          }
        />
      )}

      <Table
        loading={loading}
        rowKey={'id'}
        dataSource={listClinic}
        columns={columns}
        rowSelection={{
          selectedRowKeys,
          onChange: onSelectChange
        }}
        pagination={{
          current: dataResponse?.meta?.page || 1, // so trang
          total: dataResponse?.meta?.itemCount || 10, // tong tat ca 
          defaultPageSize: dataResponse?.meta?.take || 10,
          showSizeChanger: true,
          pageSizeOptions: ['10', '20', '50', '100'],
          locale: { items_per_page: ' kt qu/trang' },
          onChange: (page, pageSize) => {
            setPagination({
              ...pagination,
              page,
              pageSize,
            });
          },
        }}
        scroll={{ x: 'max-content' }}
      />

      <CreateClinic
        isShowModal={isModalCreate}
        handleCancelModal={() => setModalCreate(false)}
      />

      <Modal
        visible={isShowModalDelete}
        onOk={handleDeleteClinic}
        onCancel={() => setShowModalDelete(false)}
        cancelText={'Hy'}
        okText={'Xa'}
        className='confirm_delete_label'
        width={370}
      >
        <h2 style={{ color: '#595959', fontWeight: 700, textAlign: 'center' }}>
          Bn c mun xa bc s?
        </h2>
        {/* <Space direction='vertical'>
          <Text>
            Sau khi xa nhn, h thng s t ng g nhn khi cc lt tng tc  c gn nhn
            trc y.
            <br /> Vui lng cn nhc trc khi xa.
          </Text>

          <div style={{ background: '#fdefe4', padding: '10px', borderRadius: '3px' }}>
            <Text style={{ fontWeight: 600, color: '#e59935' }}>
              <WarningFilled /> Lu :
              <br />
            </Text>
            <Text>Cc nhn to t Facebook c h thng t ng ng b, khng th xa.</Text>
          </div>
        </Space> */}
      </Modal>
    </div>
  )
}

export default Clinic;