import { Button, Col, DatePicker, Form, Input, Modal, Spin, Row, Select } from 'antd';
import moment from 'moment';
import React, { useEffect, useState } from 'react';
import './CreateDoctor.scss';
import { toast } from 'react-toastify';
import ClinicApis from '../../../apis/Clinic';
import SpecialtyApis from '../../../apis/Specialty';
import DoctorApis from '../../../apis/Doctor';

const { Option } = Select;
const listGender = [
  {
    key: 'MALE',
    value: 'Nam'
  },
  {
    key: 'FEMALE',
    value: 'N'
  },
  {
    key: 'OTHER',
    value: 'Khc'
  }
]

const listRole = [
  {
    key: 'HEAD_OF_DOCTOR',
    value: 'Trng khoa'
  },
  {
    key: 'DOCTOR',
    value: 'Bc s'
  },
  {
    key: 'MANAGER_CLINIC',
    value: 'Qun l phng khm'
  }
]

const CreateDoctor = ({
  isShowModal,
  handleCancelModal, // function cancel
}) => {
  const [loading, setLoading] = useState(false);
  const [optionsClinic, setOptionsClinic] = useState([]);
  const [optionsSpecialty, setOptionsSpecialty] = useState([]);
  const [form] = Form.useForm();

  useEffect(() => {
    !!isShowModal && getListClinic();
  }, [isShowModal])

  const getListClinic = async () => {
    try {
      const dataRes = await ClinicApis.getClinics({
        pages: 1,
        take: 100,
        active: true,
      })
      // console.log('dataRes: ', dataRes);
      if (dataRes?.data?.data) {
        const { data } = dataRes?.data;
        const listOptionsClinic = data.map(item => {
          return {
            id: item.id,
            name: item?.name || '',
          }
        })
        setOptionsClinic(listOptionsClinic || []);
      }
    } catch (error) {
      console.log('error: ', error);

    }
  }
  const handleAddNewDoctor = async (value) => {
    console.log('value: ', value);
    try {
      setLoading(true)
      const dataRes = await DoctorApis.createDoctor({
        ...value,
      })
      // 
      if (dataRes.status === 200) {
        setLoading(false);
        toast.success('Thm bc thnh cng');
        handleCancelModal();
        form.setFieldsValue({
          firstName: '',
          middleName: '',
          lastName: '',
          email: '',
          gender: undefined,
          role: undefined,
          clinicId: undefined,
          specialtyId: undefined
        })
      }
    } catch (error) {
      console.log('error: ', error);
      setLoading(false);
      if (error.response.data.error === 'USER_ALREADY_EXIST' && error.response.data.status === 409) {
        toast.error('Bc s  tn ti!');
      }
      // toast.error('Li');
    }
  }

  const handleChangSelectClinic = async (value) => {
    try {
      const dataSpecialty = await SpecialtyApis.getSpecialtyByClinic(value);
      if (dataSpecialty?.data?.data?.length > 0) {
        const { data } = dataSpecialty?.data;
        const listSpecialty = data?.map(item => {
          return {
            id: item.id,
            name: item.name,
          }
        })

        setOptionsSpecialty(listSpecialty || [])
      } else if (dataSpecialty?.data?.data?.length === 0) {
        setOptionsSpecialty([]);
        form.setFieldsValue({
          specialtyId: undefined
        })
      }
    } catch (error) {
      // console.log('error: ', error);
      setOptionsSpecialty([]);
      form.setFieldsValue({
        specialtyId: undefined
      })
    }
  }


  return (
    <Modal
      className='create_modal'
      title={
        <>
          <div>Thm bc s</div>
        </>
      }
      open={isShowModal}
      onCancel={() => {
        !loading && handleCancelModal()
      }}
      width={700}
      footer={false}
    >
      <Spin spinning={loading}>
        <Form
          name='user'
          onFinish={(values) => handleAddNewDoctor(values)}
          autoComplete='off'
          layout='vertical'
          form={form}
        >
          <Row gutter={24}>
            <Col span={8}>
              <Form.Item
                name={'firstName'}
                label={<span className='txt_label'>H</span>}
                rules={[
                  {
                    required: true,
                    message: 'H khng c  trng',
                  }
                ]}
              >
                <Input
                  size='middle'
                  className='txt_input'
                  placeholder={'H'} />
              </Form.Item>
            </Col>
            <Col span={8}>
              <Form.Item
                name={'middleName'}
                label={<span className='txt_label'>Tn m</span>}
              >
                <Input
                  size='middle'
                  className='txt_input'
                  placeholder={'Tn m'} />
              </Form.Item>
            </Col>
            <Col span={8}>
              <Form.Item
                name={'lastName'}
                label={<span className='txt_label'>Tn</span>}
                rules={[
                  {
                    required: true,
                    message: 'Tn khng c  trng',
                  }
                ]}
              >
                <Input
                  size='middle'
                  className='txt_input'
                  placeholder={'Tn'} />
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item
                name={'email'}
                label={<span className='txt_label'>Email</span>}
                rules={[
                  { max: 255, message: 'Email ti a 255 k t!' },
                  {
                    required: true,
                    message: 'Email khng c  trng',
                  },
                  {
                    validator: (_, value) => {
                      const regex = new RegExp(/^[a-z0-9](\.?[a-z0-9]){2,}@gmail\.com$/gi);
                      if ((regex.test(value) && value)) {
                        return Promise.resolve();
                      } else {
                        return Promise.reject('Email khng ng nh dng');
                      }
                    },
                  },
                ]}
              >
                <Input
                  size='middle'
                  className='txt_input'
                  placeholder={'Email'} />
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item
                name={'gender'}
                label={<span className='txt_label'>Gii tnh</span>}
                rules={[
                  {
                    required: true,
                    message: 'Gii tnh khng c  trng',
                  },
                ]}
              >
                <Select
                  style={{ width: '100%' }}
                  size='middle'
                  placeholder={true ? 'Chn gii tnh' : 'Khng c thng tin'}
                  className='txt_input'
                >
                  {listGender.map((item, index) => (
                    <Option key={index} value={item.key}>
                      {item.value}
                    </Option>
                  ))}
                </Select>
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item
                name={'phoneNumber'}
                label={<span className='txt_label'>S in thoi</span>}
                rules={[
                  {
                    max: 20,
                    message: 'S in thoi ti a 20 k t',
                  },
                ]}
              >
                <Input
                  className='txt_input'
                  size='middle'
                  placeholder={true ? 'S in thoi' : 'Khng c thng tin'}
                />
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item name={'birthday'} label={<span className='txt__label'>Ngy sinh</span>}>
                <DatePicker
                  picker='date'
                  showNow={false}
                  // showTime
                  placeholder={true ? 'Chn ngy' : 'Khng c thng tin'}
                  size={'middle'}
                  format={'DD/MM/YYYY'}
                  style={{
                    width: '100%',
                    cursor: 'pointer',
                    fontSize: '14px',
                  }}
                  className='txt_input'
                  disabledDate={(current) => moment() <= current}
                />
              </Form.Item>
            </Col>
            <Col span={24}>
              <Form.Item
                name={'role'}
                label={<span className='txt_label'>Vai tr</span>}
                rules={[
                  {
                    required: true,
                    message: 'Vai tr khng c  trng',
                  },
                ]}
              >
                <Select
                  style={{ width: '100%' }}
                  size='middle'
                  placeholder={true ? 'Chn vai tr' : 'Khng c thng tin'}
                  className='txt_input'
                >
                  {listRole.map((item, index) => (
                    <Option key={index} value={item.key}>
                      {item.value}
                    </Option>
                  ))}
                </Select>
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item
                name={'clinicId'}
                label={<span className='txt_label'>Phng khm</span>}
                rules={[
                  {
                    required: true,
                    message: 'Phng khm khng c  trng',
                  },
                ]}
              >
                <Select
                  showSearch
                  style={{ width: '100%' }}
                  size='middle'
                  placeholder={true ? 'Chn phng khm' : 'Khng c thng tin'}
                  className='txt_input'
                  // optionLabelProp='label'
                  // optionFilterProp={'label'}
                  filterOption={(input, option) =>
                    option?.label !== null && option?.label?.toLowerCase().includes(input.trim().toLowerCase())
                  }
                  onChange={handleChangSelectClinic}
                >
                  {optionsClinic.length && optionsClinic.map((item) => (
                    <Option
                      key={item.id}
                      value={item.id || ''}
                      label={item.name}
                    >
                      {item.name}
                    </Option>

                  ))}
                </Select>
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item
                name={'specialtyId'}
                label={<span className='txt_label'>Chuyn khoa</span>}
              // rules={[
              //   {
              //     required: true,
              //     message: 'Gii tnh khng c  trng',
              //   },
              // ]}
              >
                <Select
                  showSearch
                  style={{ width: '100%' }}
                  size='middle'
                  placeholder={true ? 'Chn chuyn khoa' : 'Khng c thng tin'}
                  className='txt_input'
                  filterOption={(input, option) =>
                    option?.label !== null && option?.label?.toLowerCase().includes(input.trim().toLowerCase())
                  }
                >
                  {optionsSpecialty.length && optionsSpecialty.map((item) => (
                    <Option key={item.id} value={item.id}>
                      {item.name}
                    </Option>
                  ))}
                </Select>
              </Form.Item>
            </Col>
          </Row>
          <Col span={24} style={{ textAlign: 'center' }}>
            <Button className='btn_cancel' danger size='middle' onClick={() => handleCancelModal()}>
              Hy
            </Button>
            <Button className='btn_add' size='middle' htmlType='submit' type='primary'>
              Thm bc s
            </Button>
          </Col>
        </Form>
      </Spin>
    </Modal >
  )
}

export default CreateDoctor;