import { Button, Col, Form, Input, Row, Spin } from "antd";
import React, { useState } from "react";
import { toast } from "react-toastify";
import AuthApis from "../../../apis/Auth";
import './index.scss';

const ChangePassword = () => {
  const [loading, setLoading] = useState(false);
  const [form] = Form.useForm();

  const handleSubmit = async (values) => {
    setLoading(true);
    try {
      // changePassword
      const dataChange = await AuthApis.changePassword(values);
      if (dataChange?.status === 200 && dataChange?.data) {
        toast.success('i mt khu thnh cng!');
        setLoading(false);
        form.resetFields();
      }
    } catch (error) {
      console.log('error: ', error);
      setLoading(false);
      if (error?.response?.data?.error === 'CURRENT_PASSWORD_INVALID') {
        toast.error('Mt khu c khng ng!');
        return;
      } else if (error?.response?.data?.error === 'NEW_PASSWORD_AND_CONFIRM_PASSWORD_NOT_MATCH') {
        toast.error('Mt khu mi khng ging nhau!');
        return;
      }
      toast.error('i mt khu khng thnh cng!');
    }
  }

  return (
    <div className="form_change">
      <Spin spinning={loading}>
        <div style={{
          fontSize: '20px',
          textAlign: 'center'
        }}>i mt khu</div>
        <Form
          name='change_password'
          onFinish={(values) => handleSubmit(values)}
          autoComplete='off'
          layout='vertical'
          form={form}
        >
          <Row gutter={24}>
            <Col span={24}>
              <Form.Item
                name={'currentPassword'}
                label={<span className='txt_label'>Mt khu c</span>}
                rules={[
                  {
                    required: true,
                    message: 'Mt khu c khng c  trng',
                  },
                  {
                    min: 6,
                    message: 'Mt khu phi ln hn 6 k t'
                  }
                ]}
              >
                <Input.Password
                  size='middle'
                  className='txt_input'
                  placeholder={'Mt khu c'} />
              </Form.Item>
            </Col>

            <Col span={24}>
              <Form.Item
                name={'newPassword'}
                label={<span className='txt_label'>Mt khu mi</span>}
                rules={[
                  {
                    required: true,
                    message: 'Mt khu mi khng c  trng',
                  },
                  {
                    min: 6,
                    message: 'Mt khu phi ln hn 6 k t'
                  }
                ]}
              >
                <Input.Password
                  size='middle'
                  className='txt_input'
                  placeholder={'Mt khu mi'} />
              </Form.Item>
            </Col>

            <Col span={24}>
              <Form.Item
                name={'confirmPassword'}
                label={<span className='txt_label'>Xc nhn mt khu</span>}
                rules={[
                  {
                    required: true,
                    message: 'Xc nhn mt khu khng c  trng',
                  },
                  {
                    min: 6,
                    message: 'Mt khu phi ln hn 6 k t'
                  }
                ]}
              >
                <Input.Password
                  size='middle'
                  className='txt_input'
                  placeholder={'Xc nhn mt khu'} />
              </Form.Item>
            </Col>
            <Col span={24} style={{ textAlign: 'center' }}>
              <Button className='btn_add' htmlType='submit' type='primary' size='middle' >
                Xc nhn
              </Button>
            </Col>
          </Row>

        </Form>
      </Spin>
    </div>
  )
}

export default ChangePassword;