import { Avatar, Badge, Button, Col, DatePicker, Form, Input, message, Row, Select, Space, Spin, Typography, Upload } from "antd";
import moment from "moment";
import React, { useState } from "react";
import { toast } from "react-toastify";
import UserApis from "../../../apis/User";
import baseURL from "../../../utils/url";
import { listGender } from '../../../common/constants/gender';
import './InformationAdmin.scss';
import { useEffect } from "react";
const { Text, Title } = Typography;
const { Option } = Select;



const InformationAdmin = ({ dataUser, handleReset }) => {
  const [loadingAvatar, setLoadingAvatar] = useState(false);
  const [loadingUpdate, setLoadingUpdate] = useState(false);
  const [editInformation, setEditInformation] = useState(false);

  const [form] = Form.useForm();

  // const props = {
  //   beforeUpload: file => {
  //     console.log('file: ', file);
  //     const listType = ['image/jpeg', 'image/png', 'image/jpg'];
  //     const checkType = listType.includes(file.type)

  //     if (!checkType) {
  //       message.error(`Khng ng nh dng nh`);
  //       return;
  //     }
  //     return checkType;
  //   },
  //   onChange: info => {
  //     console.log('onchange: ', info.fileList);
  //   },
  // };

  useEffect(() => {
    if (dataUser) {
      form.setFieldsValue({
        firstName: dataUser?.firstName || '',
        middleName: dataUser?.middleName || '',
        lastName: dataUser?.lastName || '',
        email: dataUser?.email || '',
        gender: dataUser?.gender || null,
        phoneNumber: dataUser?.phoneNumber || null,
        birthday: dataUser?.birthday ? moment(dataUser?.birthday) : null,
        address: dataUser?.address || '',
        role: dataUser.role || '',
        religion: dataUser?.religion || '',
        nation: dataUser?.nation || '',
        identityCardNumber: dataUser?.identityCardNumber || '',
      })
    }
  }, [dataUser])

  const handleUploadAvatar = async ({ file, fileList }) => {
    setLoadingAvatar(true);
    console.log('file: ', file);
    console.log('fileList: ', fileList);
    const listType = ['image/jpeg', 'image/png', 'image/jpg'];
    const checkType = listType.includes(file.type)

    if (!checkType) {
      message.error(`Khng ng nh dng nh`);
      return;
    }

    const fileSize = file?.size;
    const isLt2M = fileSize / 1024 / 1024 < 2;
    // console.log('isLt2M: ', isLt2M);
    if (!isLt2M) {
      message.error('Chn nh nh hn 2MB!');
      return;
    }

    try {
      const formData = new FormData();
      formData.append('file', fileList[0]?.originFileObj);

      const data = await UserApis.changeAvatar(formData);
      if (data?.status === 200 && data?.data) {
        toast.success('Thay i nh i din thnh cng');
        setLoadingAvatar(false);
        handleReset();
      }
    } catch (error) {
      console.log('error: ', error);
      toast.success('Thay i nh i din khng thnh cng');
      setLoadingAvatar(false);
    }
  }

  const handleSubmitStaff = async (values) => {
    setLoadingUpdate(true)
    try {
      const dataUpdate = await UserApis.updateUser(values, dataUser.id);
      if (dataUpdate.status === 200 && dataUpdate?.data) {
        setLoadingUpdate(false);
        toast.success('Cp nht thng tin nhn vin thnh cng');
        setEditInformation(false);
      }
    } catch (error) {
      console.log('error: ', error);
      if (error.response.data.error === 'USER_ALREADY_EXIST' && error.response.data.status === 409) {
        toast.error('Bc s  tn ti!');
        return;
      }
      setLoadingUpdate(false);
      toast.error('Cp nht thng tin khng thnh cng!');
    }
  }

  return (
    <div>
      <div className="info_admin_avatar">
        <Space align='center'>
          <Badge
            offset={[-10, 66]}
          >
            {loadingAvatar ? (
              <Spin className='spin-avatar' />
            ) : (
              <Form.Item
                noStyle
                shouldUpdate={(prev, cur) => {
                  return prev.avatar !== cur.avatar;
                }}
              >
                <Avatar
                  src={`${baseURL}${dataUser.avatar}`}
                  size={80}
                  style={{ fontSize: '26px' }}
                >
                  {dataUser?.lastName?.[0]?.toUpperCase()}
                </Avatar>

              </Form.Item>
            )}
          </Badge>
          <Space direction='vertical' size={0} style={{ marginLeft: '10px' }}>
            <Title level={5} style={{ color: 'rgba(17, 17, 17, 0.75)', fontWeight: 600 }}>
              nh i din
            </Title>
            {/* <label htmlFor='upload-input'>
                  <Text style={{ color: '#3863EF', fontWeight: 500, cursor: 'pointer' }} underline>
                    Thay nh ...
                  </Text>
                </label> */}
            <Upload
              // {...props}
              // listType="picture-card"
              maxCount={1}
              showUploadList={false}
              multiple={false}
              beforeUpload={() => false}
              onChange={handleUploadAvatar}
            >
              <span
                style={{
                  color: '#3863EF',
                  fontWeight: 500,
                  cursor: 'pointer'
                }}
              >Thay nh...</span>
            </Upload>
          </Space>
        </Space>
      </div>

      <div className="info_admin_content">
        <div className="info_admin_content_header">
          <div style={{ fontSize: '17px' }}>Thng tin</div>
          <div className='button_edit'>
            {!editInformation && (
              <Button
                type='primary'
                onClick={() => setEditInformation(true)}
              >
                Chnh sa thng tin
              </Button>
            )}
          </div>
        </div>

        <div className="form_info_admin">
          <Spin className="spin_form" spinning={loadingUpdate}>
            <Form
              name='user'
              onFinish={(values) => handleSubmitStaff(values)}
              autoComplete='off'
              layout='vertical'
              form={form}
            >
              <Row gutter={24}>
                <Col span={8}>
                  <Form.Item
                    name={'firstName'}
                    label={<span className='txt_label'>H</span>}
                    rules={[
                      {
                        required: true,
                        message: 'H khng c  trng',
                      }
                    ]}
                  >
                    <Input
                      disabled={!editInformation}
                      size='middle'
                      className='txt_input'
                      placeholder={'H'} />
                  </Form.Item>
                </Col>
                <Col span={8}>
                  <Form.Item
                    name={'middleName'}
                    label={<span className='txt_label'>Tn m</span>}
                  // rules={[
                  //   {
                  //     required,
                  //     message: 'H khng c  trng',
                  //   }
                  // ]}
                  >
                    <Input
                      disabled={!editInformation}
                      size='middle'
                      className='txt_input'
                      placeholder={dataUser?.middleName ? 'Tn m' : 'Khng c thng tin'} />
                  </Form.Item>
                </Col>
                <Col span={8}>
                  <Form.Item
                    name={'lastName'}
                    label={<span className='txt_label'>Tn</span>}
                    rules={[
                      {
                        required: true,
                        message: 'Tn khng c  trng',
                      }
                    ]}
                  >
                    <Input
                      disabled={!editInformation}
                      size='middle'
                      className='txt_input'
                      placeholder={'Tn'} />
                  </Form.Item>
                </Col>
                <Col span={8}>
                  <Form.Item
                    name={'email'}
                    label={<span className='txt_label'>Email</span>}
                    rules={[
                      { max: 255, message: 'Email ti a 255 k t!' },
                      {
                        required: true,
                        message: 'Email khng c  trng',
                      },
                      {
                        validator: (_, value) => {
                          const regex = new RegExp(/^[a-z0-9](\.?[a-z0-9]){2,}@gmail\.com$/gi);
                          if ((regex.test(value) && value)) {
                            return Promise.resolve();
                          } else {
                            return Promise.reject('Email khng ng nh dng');
                          }
                        },
                      },
                    ]}
                  >
                    <Input
                      disabled={!editInformation}
                      size='middle'
                      className='txt_input'
                      placeholder={'Email'} />
                  </Form.Item>
                </Col>
                <Col span={8}>
                  <Form.Item
                    name={'gender'}
                    label={<span className='txt_label'>Gii tnh</span>}
                    rules={[
                      {
                        required: true,
                        message: 'Gii tnh khng c  trng',
                      },
                    ]}
                  >
                    <Select
                      disabled={!editInformation}
                      style={{ width: '100%' }}
                      size='middle'
                      placeholder={dataUser?.gender ? 'Chn gii tnh' : 'Khng c thng tin'}
                      className='txt_input'
                    >
                      {listGender.map((item, index) => (
                        <Option key={index} value={item.key}>
                          {item.value}
                        </Option>
                      ))}
                    </Select>
                  </Form.Item>
                </Col>
                <Col span={8}>
                  <Form.Item
                    name={'role'}
                    label={<span className='txt_label'>Vai tr</span>}
                    rules={[
                      {
                        required: true,
                        message: 'Vai tr khng c  trng',
                      },
                    ]}
                  >
                    <Select
                      disabled={true}
                      style={{ width: '100%' }}
                      size='middle'
                      placeholder={dataUser?.role ? 'Chn vai tr' : 'Khng c thng tin'}
                      className='txt_input'
                    >
                      {[{ key: 'ADMIN', value: 'Admin' }].map((item, index) => (
                        <Option key={index} value={item.key}>
                          {item.value}
                        </Option>
                      ))}
                    </Select>
                  </Form.Item>
                </Col>
                <Col span={12}>
                  <Form.Item
                    name={'phoneNumber'}
                    label={<span className='txt_label'>S in thoi</span>}
                    rules={[
                      {
                        max: 20,
                        message: 'S in thoi ti a 20 k t',
                      },
                    ]}
                  >
                    <Input
                      disabled={!editInformation}
                      className='txt_input'
                      size='middle'
                      placeholder={dataUser?.phoneNumber ? 'S in thoi' : 'Khng c thng tin'}
                    />
                  </Form.Item>
                </Col>
                <Col span={12}>
                  <Form.Item name={'birthday'} label={<span className='txt__label'>Ngy sinh</span>}>
                    <DatePicker
                      picker='date'
                      showNow={false}
                      disabled={!editInformation}
                      // showTime
                      placeholder={dataUser?.birthday ? 'Chn ngy' : 'Khng c thng tin'}
                      size={'middle'}
                      format={'DD/MM/YYYY'}
                      style={{
                        width: '100%',
                        cursor: 'pointer',
                        fontSize: '14px',
                      }}
                      className='txt_input'
                      disabledDate={(current) => moment() <= current}
                    />
                  </Form.Item>
                </Col>
                <Col span={8}>
                  <Form.Item
                    name={'nation'}
                    label={<span className='txt_label'>Quc gia</span>}
                  >
                    <Input
                      disabled={!editInformation}
                      className='txt_input'
                      size='middle'
                      placeholder={dataUser?.nation ? 'Quc gia' : 'Khng c thng tin'}
                    />
                  </Form.Item>
                </Col>
                <Col span={8}>
                  <Form.Item
                    name={'religion'}
                    label={<span className='txt_label'>Tn gio</span>}
                  >
                    <Input
                      disabled={!editInformation}
                      className='txt_input'
                      size='middle'
                      placeholder={dataUser?.religion ? 'Tn gio' : 'Khng c thng tin'}
                    />
                  </Form.Item>
                </Col>
                <Col span={8}>
                  <Form.Item
                    name={'identityCardNumber'}
                    label={<span className='txt_label'>CCCD/CTM</span>}
                  >
                    <Input
                      disabled={!editInformation}
                      className='txt_input'
                      size='middle'
                      placeholder={dataUser?.identityCardNumber ? 'CCCD/CMT' : 'Khng c thng tin'}
                    />
                  </Form.Item>
                </Col>

                <Col span={24}>
                  <Form.Item
                    name={'address'}
                    label={<span className='txt_label'>a ch</span>}
                    rules={[
                      {
                        max: 255,
                        message: 'a ch ti a 255 k t',
                      },
                    ]}
                  >
                    <Input
                      disabled={!editInformation}
                      className='txt_input'
                      size='middle'
                      placeholder={dataUser?.address ? 'a ch' : 'Khng c thng tin'}
                    />
                  </Form.Item>
                </Col>
              </Row>
              {editInformation && (
                <div style={{ textAlign: 'center', marginTop: '30px', width: '100%' }}>
                  <Col span={24} style={{ textAlign: 'center' }}>
                    <Button className='btn_cancel' danger size='middle'>
                      Hy
                    </Button>

                    <Button className='btn_add' size='middle' htmlType='submit' type='primary'>
                      Lu thay i
                    </Button>
                  </Col>
                </div>
              )}
            </Form>
          </Spin>
        </div>
      </div>

    </div>
  )
}

export default InformationAdmin;